.card {
    width: 80%;
    background-color: #fff;
    background: linear-gradient(#f8f8f8, #fff);
    box-shadow: 0 8px 16px -8px rgba(0,0,0,0.4);
    border-radius: 6px;
    border: 2px solid gray;
    overflow: hidden;
    position: relative;
    margin: 1.5rem;
}

.card h1 {
    margin-bottom: 0;
}

.card h2 {
    font-size: 15px;
    color: darkgrey;
    margin-bottom: 0;
}

.card hr {
    margin-bottom: 0;
}

