#menu {
    padding: 1rem;

    background: black;
    height: 50%;

    display: flex;
    justify-content: end;
    align-items: center;
    flex-wrap: nowrap;
    flex-direction: row;

    font-size: 16px;
}

.link {
    text-decoration: none;
    background:
            linear-gradient(currentColor 0 0)
            var(--p, 0) 100% /var(--d, 0) 2px
            no-repeat;
    transition: 0.3s, background-position 0s 0.3s;
    color: white;
    margin: 15px;
}

.link:hover {
    --d: 100%;
    --p: 100%;
}


