.card-resume {
    width: 30%;
    background-color: #fff;
    background: linear-gradient(#f8f8f8, #fff);
    box-shadow: 0 8px 16px -8px rgba(0,0,0,0.4);
    border-radius: 6px;
    border: 2px solid gray;
    overflow: hidden;
    position: relative;
    margin: 1.5rem;
}

.card-resume h1 {
    margin-bottom: 0;
}

.card-resume h2 {
    font-size: 15px;
    color: darkgrey;
    margin-bottom: 0;
}

.card-resume hr {
    margin-bottom: 0;
}

hr {
    width: 10rem;
}

.card-inside {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: space-between;
}

.card-content > p {
    margin: 0 1rem 0 1rem;
}


