#hero {
    height: 650px;
    width: 100%;
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: space-between;
    color: white;
    font-size: 40px;
}

li {
    list-style: none;
}

.ornament-container-l {
    height: 40%;
    margin-left: 2rem;
}

.ornament-container-l > img {
    height: 100%;
    filter: sepia(100%);
}

.ornament-container-r {
    height: 40%;
    margin-right: 2rem;
}

.ornament-container-r > img {
    height: 100%;
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
    filter: sepia(100%);
}
