.timelineContainer {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: center;
    width: 80%;
}

.timelineRow-right {
    display: flex;
    flex-direction: row-reverse;
    text-align: center;
    width: 100%;
}

.timelineRow-left {
    display: flex;
    flex-direction: row;
    text-align: center;
    width: 100%;
}

.timelineEntry-right {
    border-left: 2px black solid;
    width: 100%;
}

.timelineEntry-left {
    border-right: 2px black solid;
    width: 100%;
}

.dot {
    background-color: black;
    width: 100%;
    height: 2px;
}
