@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@600&display=swap');

@font-face {
  font-family: "Belgiante";
  src: url("assets/font/Belgiante.ttf") format("truetype");
}

@font-face {
  font-family: "RobotoSlab";
  src: url("assets/font/RobotoSlab-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "LeagueSpartan";
  src: url("assets/font/LeagueSpartan-Bold.otf") format("truetype");
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: "RobotoSlab";
  padding: 0;
  margin: 0;
}

h1 {
  padding: 0;
  margin: 0;
}

html {
  font-size: 20px;
  overflow: scroll;
  overflow-x: hidden;
}

#content {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 100%;
}

#content h1 {
color: #281034;
}

.sectionTitle {
  padding: 4px;
  font-size: 40px;
}
