.fade-enter {
    width: 100%;
    opacity: 0;
    z-index: 1;
}

.fade-enter.fade-enter-active {
    opacity: 1;
    transition: opacity 250ms ease-in;
}

#content > div {
    width: 100%;
    text-align: center;
    justify-content: center;
}

#content {
    display: flex;
    flex-direction: column;
}


